.button {
    width: 100px;
    height: 35px;
    font-family: 'Roboto', sans-serif;
    font-size: 11px;
    font-weight: 800;
    color: #000;
    background-color: aqua;
    border: none;
    border-radius: 45px;
    box-shadow: 0px 8px 15px rgba(0, 0, 0, 0.2);
    cursor: pointer;
    outline: none;
    margin-right: 5px;
}

